export default ()=> {
      return (
        <div className="question" data-index="99" dangerouslySetInnerHTML = {{ __html: `
<h6>101.输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> one <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token boolean">false</span> <span class="token operator">||</span> <span class="token punctuation">{</span><span class="token punctuation">}</span> <span class="token operator">||</span> <span class="token keyword keyword-null">null</span><span class="token punctuation">)</span>
<span class="token keyword keyword-const">const</span> two <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token keyword keyword-null">null</span> <span class="token operator">||</span> <span class="token boolean">false</span> <span class="token operator">||</span> <span class="token string">""</span><span class="token punctuation">)</span>
<span class="token keyword keyword-const">const</span> three <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token punctuation">]</span> <span class="token operator">||</span> <span class="token number">0</span> <span class="token operator">||</span> <span class="token boolean">true</span><span class="token punctuation">)</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>one<span class="token punctuation">,</span> two<span class="token punctuation">,</span> three<span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>false</code> <code>null</code> <code>[]</code></li>
<li>B: <code>null</code> <code>&quot;&quot;</code> <code>true</code></li>
<li>C: <code>{}</code> <code>&quot;&quot;</code> <code>[]</code></li>
<li>D: <code>null</code> <code>null</code> <code>true</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p>使用<code>||</code>运算符，我们可以返回第一个真值。 如果所有值都是假值，则返回最后一个值。</p>
<p><code>（false || {} || null）</code>：空对象<code>{}</code>是一个真值。 这是第一个（也是唯一的）真值，它将被返回。<code>one</code>等于<code>{}</code>。</p>
<p><code>（null || false ||“”）</code>：所有值都是假值。 这意味着返回传递的值<code>&quot;&quot;</code>。 <code>two</code>等于<code>&quot;&quot;</code>。</p>
<p><code>（[] || 0 ||“”）</code>：空数组<code>[]</code>是一个真值。 这是第一个返回的真值。 <code>three</code>等于<code>[]</code>。</p>
</p>
</details>
` }}></div>
      );
    }